@import "variables.scss"; // переменные

.intro-box {
  user-select: none;
}

@media #{$mobile-big} {

  h1,
  .h1 {
    font-size: $font-size-h1;
  }

  h2,
  .h2 {
    font-size: floor($font-size-h2 / 1.1);
    line-height: 1.3;
  }

  h3,
  .h3 {
    font-size: $font-size-h3;
  }

  h4,
  .h4 {
    font-size: $font-size-h4;
  }

  h5,
  .h5 {
    font-size: $font-size-h5;
  }

  h6,
  .h6 {
    font-size: $font-size-h6;
  }
}

.one-click__bgbox {
  max-width: 100%;
}

.owl-wrap_outside {
  max-width: 100%;

  @media #{$tablet_gt} {
    overflow: hidden;
  }

  @media #{$tablet} {
    overflow: hidden;
  }

  @media #{$tablet_lt} {
    overflow: hidden;
  }
}

.owl-stage-outer {
  max-width: 100%;

  @media #{$tablet_gt} {
    overflow: hidden;
    .owl-stage {
      display: flex;
    }
  }
}


@media #{$tablet} {

  .header,
  .footer,
  .company-awards,
  .start-trading,
  .security-of-means,
  .top-traders,
  .trading-market,
  .main-features,
  .main-world {
    &__wrap {
      max-width: $max-width-tablet;
    }
  }

  .popular-terminal__headline .h2,
  .intro-box .h2 {
    font-size: 30px;
    line-height: 1.3;
  }

  .one-click__step,
  .intro-box.slash p,
  .main-world__title .h2 {
    font-size: 20px;
  }

  .main-world__body p,
  .cut-bubble-message__content p,
  .list-check li,
  .icon-card__content p,
  .one-click__body,
  .intro-box p,
  .advantage-cards--shadow .advantage-card__body {
    font-size: 16px;
    line-height: 1.4;
  }

  .footer__wrap {
    .footer-navigation__item {
      flex-basis: 33.3%;
      max-width: 33.3%;
      min-width: 33.3%;

      &:last-child {
        flex-basis: 25%;
        max-width: 25%;
        min-width: 25%;
      }
    }

    .footer-navigation__items {
      flex-basis: 40%;
      flex-wrap: wrap;

      .footer-navigation__item {
        flex-basis: 100%;
        min-width: 100%;
        max-width: 100%;
        margin-bottom: 30px;
      }
    }

    &.footer-docs__wrap {
      padding-bottom: 5px;
      padding-left: 50px;
    }

    .footer-docs__list {
      flex-wrap: wrap;

      .doc-link {
        margin-bottom: 25px;
        flex-basis: 50%;
        max-width: 50%;
        min-width: 50%;
      }
    }

    .footer-disclaimer__icon {
      margin-right: 25px;
    }

    &.footer-info__wrap {
      max-width: 640px;
    }

    &.footer-payment-options__wrap {
      padding-left: 0;
      padding-right: 0;
      max-width: 710px;
    }

    .footer-payment-options__item {
      text-align: center;
      padding-left: 10px;
      padding-right: 10px;
    }

    .owl-item {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  ._horizontal_scroll_mobile {
    .owl-nav.disabled {
      display: none;
    }

    .owl-stage-outer {
      max-width: 100%;
      overflow: hidden;

      .owl-stage {
        display: flex;
      }
    }
  }
}

.mobile-visible {
  display: none !important;
}

@media #{$tablet_lt} {
  .mobile-hidden {
    display: none !important;
  }

  .mobile-visible {
    display: initial !important;
  }

  .intro-box p {
    font-size: 14px;
    line-height: 1.6;
  }

  .security-of-means__content {
    .intro-box.p-decor p {
      padding-left: 20px;
      margin-top: 0;

      &:before {
        width: 20px;
        top: 0;
        left: 0;
        transform: rotate(120deg);
      }
    }
  }

  .header,
  .footer,
  .company-awards,
  .start-trading,
  .security-of-means,
  .top-traders,
  .one-click,
  .trading-market,
  .why-we,
  .main-features,
  .main-world {
    &__wrap {
      max-width: 100%;
      min-width: 100%;
    }
  }

  .popular-terminal {
    padding-top: 30px;
    padding-bottom: 40px;

    &__wrap {
      padding-left: 30px;
      padding-right: 30px;
      flex-wrap: wrap;

      .col-half {
        width: 100%;
      }
    }

    &__screen {
      display: none;
    }

    p {
      font-size: 14px;
      line-height: 1.8;
    }

    &-actions {
      align-items: center;
      align-content: center;
    }

    &-actions__buttons {
      flex-wrap: wrap;
      width: 100%;

      .button {
        margin-left: auto !important;
        margin-right: auto !important;
        width: 100%;
        max-width: 220px;
        margin-bottom: 20px;
        min-height: 50px;
        justify-content: center;

        .button-with-icon__icon {
          width: 30px;

          svg {
            width: 30px;
            height: 35px;
          }
        }

        .button-with-icon__text {
          text-align: left;
          min-width: 90px;
        }
      }
    }
  }

  .popular-terminal,
  .intro-box {
    .h2 {
      font-size: 20px;
      line-height: 1.4;
    }
  }

  .headline-features {
    flex-wrap: wrap;

    &__bg {
      height: 110%;
    }
  }

  .main-features__wrap {
    max-width: 100%;
    overflow: hidden;
  }

  .main-features__cards {
    max-width: 100%;
    // padding-left: 30px;
  }

  .headline-slider {
    background: #232D3A;

    .owl-dots {
      top: 523px;
      bottom: auto;
    }

    .headline__picture {
      display: none;
    }
  }

  .headline-slider,
  .headline-slider__item {
    height: 860px;
  }

  .headline,
  .headline--main {
    padding-left: 20px;

    .headline__content p {
      padding-left: 0;
    }
  }

  .headline-slider .headline-slider__item .headline--main,
  .headline-slider .headline-slider__item .headline {
    background-image:
      linear-gradient(-12deg, #232D3A 70px, #232D3A 87px, #4A5261 88px, #4A5261 145px, #EA252C 146px, #EA252C 271px, transparent 272px, transparent 100%),
    ;
    background-position: 0 0;
    background-repeat: no-repeat;
  }

  .headline--bitcoin {
    // background-image:
    //   linear-gradient(-12deg, #232D3A 70px, #232D3A 87px, #4A5261 88px, #4A5261 145px, #EA252C 146px, #EA252C 271px, transparent 272px, transparent 100%),
    //   url(../images/main/headline/bitcoin_mobile.png);
    // background-position: 0 0, calc(100% + 20px) 20px;

    &:before {
      display: none;
    }
  }

  .headline__wrap {
    padding-top: 40px;
  }

  .headline-slider .headline-slider__item {
    .headline__content {
      max-width: 100%;

      .h2 {
        font-size: 24px;
        line-height: 1.3;
        margin-bottom: 50px;
        padding-right: 10px;
      }

      p {
        font-size: 14px;
        margin-bottom: 30px;

        &:before {
          top: -22px;
        }
      }
    }

    .horisontal-list {
      flex-wrap: wrap;
      padding-left: 0;
      margin-bottom: 30px;

      li {
        min-width: calc(40% - 20px);
      }
    }

    .headline-actions {
      padding-left: 0;

      .button {
        min-width: 175px;
        padding-left: 14px;
        padding-right: 14px;
        margin-bottom: 13px;
        border: 1px solid white;
      }

      .button-ghost {
        background-color: #EA252C;

        &:hover {
          background-color: #F24B51;
        }
      }
    }

    .headline__award {
      position: static;
      max-width: 270px;
      margin: 20px auto 0 20px;
    }
  }

  .headline-slider__wrap .headline-features {
    bottom: 50px;
    justify-content: center;

    .headline-feature {
      width: 230px;
      padding-right: 0;
      margin-right: 0;
      margin-top: 30px;

      &__icon {
        width: 60px;
        flex-basis: 60px;
        max-width: 60px;
        min-width: 60px;
        text-align: center;

        svg {
          width: 40px;
        }
      }

      &__description {
        max-width: 100%;
      }
    }
  }


  .main-world__wrap {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  .cut-bubble-message__content {
    .h2 {
      font-size: 20px;
      line-height: 1.4;
    }
  }

  .cut-bubble-message__content {
    p {
      font-size: 16px;
      line-height: 1.3;
    }
  }

  .main-world .intro-box {
    .h2 {
      max-width: 260px;
      font-size: 20px;
      line-height: 1.4;
      margin-bottom: 0;

      span {
        display: inline;
      }
    }
  }

  .main-world {
    &__title {
      position: relative;
      left: unset;
      top: unset;

      .h2 {
        font-size: 16px;
        text-align: left;
        margin-left: 0;
      }

      &:after {
        right: auto;
        bottom: -21px;
        left: -10px;
        width: 26px;
      }
    }

    &__content {
      flex-wrap: wrap;

      .col-half {
        width: 100%;
      }
    }

    &__content-box {
      padding-left: 0;
    }

    .main-world__body {
      padding: 11px 0 30px 20px;

      p {
        font-size: 14px;
      }
    }
  }

  .icon-cards--white .icon-card {
    flex-basis: 100%;
    max-width: 100%;
    flex: 1 1 100%;
    flex-wrap: nowrap;
    margin-bottom: 0;
    width: 100%;
  }

  .cut-bubble {
    &--main {
      padding-top: 66px;
      margin-top: -66px;
      overflow: hidden;
    }

    .cut-bubble__wrap {
      flex-wrap: wrap;
      max-width: 100%;

      .col-half {
        width: 100%;
        flex-basis: 100%;
      }
    }

    .cut-bubble-message--main {
      margin-top: -30px;

      &:after {
        bottom: -115px;
        right: -20px;
      }

      .cut-bubble-message__box {
        max-width: calc(100% - 20px);
      }

      .h2 span {
        display: inline;
      }

      p {
        max-width: 160px;
        margin-left: auto;
        margin-right: 0;
      }
    }

    .cut-bubble-picture {
      z-index: 2;
      padding-left: 30px;
      margin-top: -20px;
    }
    .cut-bubble__picture {
      text-align: center;
    }
  }

  .footer__wrap {
    &.footer-payment-options__wrap {
      padding-left: 0;
      padding-right: 0;
      max-width: 100%;
      overflow: hidden;
    }

    .footer-payment-options__item {
      text-align: center;
      padding-left: 10px;
      padding-right: 10px;
      min-width: 100px;
      display: flex;
      align-self: center;
      align-items: center;
    }

    .owl-item {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .footer-navigation__item {
      flex-basis: 100%;
      max-width: 100%;
      min-width: 100%;

      &:last-child {
        flex-basis: 100%;
        max-width: 100%;
        min-width: 100%;
      }
    }

    .footer-navigation__items {
      flex-basis: 100%;
      flex-wrap: wrap;
      max-width: 100%;
      margin-top: 30px;

      .footer-navigation__item {
        flex-basis: 100%;
        min-width: 100%;
        max-width: 100%;
        margin-bottom: 30px;
      }
    }
  }

  ._horizontal_scroll_mobile_sm,
  ._horizontal_scroll_mobile {
    .owl-nav.disabled {
      display: none;
    }

    .owl-stage-outer {
      max-width: 100%;

      .owl-stage {
        display: flex;
      }
    }
  }

  .why-we {
    overflow: hidden;
    padding-top: 20%;
    padding-bottom: 20%;
    margin-bottom: -46px;
    position: relative;
    padding-left: 20px;
    position: relative;
    z-index: 2;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      background: url(../images/svg/angle.svg) 50% 0 no-repeat;
      background-size: 115% auto;
      padding-bottom: 20%;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: url(../images/svg/angle.svg) 50% 100% no-repeat;
      background-size: 115% auto;
      padding-bottom: 30%;
    }

    .why-we__lists {
      margin-top: 0;
    }

    .h3 {
      font-size: 16px;
      line-height: 1.4;
      padding-left: 70px;
    }

    .list-check li {
      font-size: 14px;
      line-height: 1.4;
    }

    .why-we__list {
      min-width: 300px;
      flex-basis: 100%;
      max-width: 100%;
      padding-left: 0;
      padding-right: 20px;
      padding-top: 30px;

      &:before {
        width: 30px;
        height: 22px;
        left: 20px;
      }
    }

    .owl-stage {
      display: flex;
    }
  }

  .trading-market {
    margin-top: -20px;
    padding-top: 80px;

    .trading-market__bg {
      &-red {
        background-color: #EA252C;
        transform: skewY(13deg);
      }

      &-light {
        background-color: #F3F4F5;
        transform: skewY(-13deg);
      }
    }
  }

}

@media (max-width: 470px) {

  .t-market-control__icon {
    max-width: 48px;
    max-height: 48px;

    svg {
      width: 48px;
      height: 48px;
      max-width: 28px;
      max-height: 28px;
    }
  }


  .trading-market__controls-img span {
    &:nth-child(1) {
      .t-market-control {
        right: 0;
        top: 80px;
        .t-market-control__name {
          width: 65px;
        }
      }
    }

    &:nth-child(2) {
      .t-market-control {
        left: 75px;
        top: -14px;
      }
    }

    &:nth-child(3) {
      .t-market-control {
        top: 5px;
        right: 6px;
      }
    }

    &:nth-child(4) {
      .t-market-control {
        bottom: 20px;
        .t-market-control__name {
          left: -7px;
          top: 5px;
          text-align: center;
        }
      }
    }

    &:nth-child(5) {
      .t-market-control {
        bottom: 6px;
        left: 8px;
      }
    }

    &:nth-child(6) {
      .t-market-control {
        left: 114px;
        .t-market-control__name {
          top: 7px;
          left: -3px;
        }
      }
    }
  }
}

@media (max-width: 679px) {

  .headline-slider,
  .headline-slider__item {
    height: auto;
  }

  .headline-slider {
    background: #222B34;
    .headline-features {
      position: static;
      padding-bottom: 50px;
    }

    .headline-slider__list {
      position: relative;
    }

    .owl-dots {
      top: auto;
      bottom: 57px;
    }

    .headline__picture {
      display: none;
    }
  }
}
